/**
 * js-sdk
 * https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
 */
import wx from 'weixin-js-sdk'
import Axios from 'axios'
const wxApi = {
  /**
  * [wxRegister Api]
  * @param  {Function} callback [ready]
  */
  wxRegister (callback) {
    // 
    Axios.post('/api/wechat/shares', { reqUrl: window.location.href }, { timeout: 5000, withCredentials: true }).then((res) => {
      let data = JSON.parse(res.data.data) // PS: 
      wx.config({
        debug: false, // 
        appId: data.appId, // 
        timestamp: data.timestamp, // 
        nonceStr: data.noncestr, // 
        signature: data.signature, // 1
        jsApiList: [onMenuShareAppMessage, onMenuShareQQ,onMenuShareWeibo,onMenuShareQZone] // JSJS2
      })
    }).catch((error) => {
      console.log(error)
    })
    wx.ready((res) => {
      // ready
      if (callback) {
        callback()
      }
    })
  },
  /**
  * [ShareTimeline ]
  * @param {[type]} option []
  * @param {[type]} success []
  * @param {[type]} error   []
  */
  ShareTimeline (option) {
    wx.onMenuShareTimeline({
      title: option.title, // 
      link: option.link, // 
      imgUrl: option.imgUrl, // 
      success () {
        // 
        option.success()
      },
      cancel () {
        // 
        option.error()
      }
    })
  },
  /**
  * [ShareAppMessage ]
  * @param {[type]} option []
  * @param {[type]} success []
  * @param {[type]} error   []
  */
  ShareAppMessage (option) {
    wx.onMenuShareAppMessage({
      title: option.title, // 
      desc: option.desc, // 
      link: option.link, // 
      imgUrl: option.imgUrl, // 
      success () {
        // 
        option.success()
      },
      cancel () {
        // 
        option.error()
      }
    })
  }
}
export default wxApi


