<template>
  <div class="home h-ui-auto">
      
      <swiper :options="swiperOption" ref="mySwiper" effect="fade" class="h">
        <!-- slides -->
        <swiper-slide><img src="../assets/1.jpg"/></swiper-slide>
        <swiper-slide><img src="../assets/2.jpg"/></swiper-slide>
        <swiper-slide><img src="../assets/3.jpg"/></swiper-slide>
        <swiper-slide><img src="../assets/4.jpg"/></swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
        <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
        <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
      </swiper>

      <div class="ui-item-txt text-center">
          <h4>{{$t("houseTit")}}</h4>
          <p class="ui-item-des text-left">{{$t("houseOne")}}</p>
          <p class="ui-item-des text-left">{{$t("houseTwo")}}</p>
      </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import wxapi from '../../common/wxapi.js'
export default {
  name: 'house',
  data () {
    return {
      swiperOption: {
        lazy: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true // 
        },
        autoplay: {
          delay: 3000,
          disableOnInteraction: false // 
        },
        effect : 'fade',//
          fade: {
            crossFade: true,//true
          },
        loop: true,//
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    },
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted() {
    // current swiper instance
    // swiper
    console.log('this is current swiper instance object', this.swiper)
    this.swiper.slideTo(3, 1000, false)
    wxapi.wxRegister(this.wxRegCallback)
  },
  methods: {
   wxRegCallback () {
      // JS-SDK
      this.wxShareTimeline()
      this.wxShareAppMessage()
    },
    wxShareTimeline () {
      // 
      let option = {
        title: ' ', // , 
        link: window.location.href.split('#')[0], // split
        imgUrl: 'logo.png', // , 
        success: () => {
          alert('')
        },
        error: () => {
          alert('')
        }
      }
      // 
      wxapi.ShareTimeline(option)
    },
    wxShareAppMessage () {
      // 
      let option = {
        title: ' ', // , 
        desc: ' ', // , 
        link: window.location.href.split('#')[0], // split
        imgUrl: 'logo.png', // , 
        success: () => {
          alert('')
        },
        error: () => {
          alert('')
        }
      }
      // 
      wxapi.ShareAppMessage(option)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  
  

</style>



